import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {SectionModel} from '../../models/section.model';

@Component({
  selector: 'app-forth',
  templateUrl: './forth.component.html',
  styleUrls: ['./forth.component.scss']
})
export class ForthComponent implements OnInit {
  @Output() next: EventEmitter<SectionModel> = new EventEmitter<SectionModel>();
  values: string[] = ['Quality', 'Consistency', 'Reusability', 'Efficiency', 'Standard Programming', 'Modular Programming', 'Mobile first'];

  constructor() {
  }

  ngOnInit(): void {
  }

  scrollToNext(event: any): void {
    this.next.emit({
      icon: 'message',
      id: 'fifth',
      selected: false
    });
  }
}
