import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {SectionModel} from '../../models/section.model';
import {PROJECTS} from '../../constants/projects.const';
import {ProjectType} from '../types/project.type';

@Component({
  selector: 'app-second',
  templateUrl: './second.component.html',
  styleUrls: ['./second.component.scss']
})


export class SecondComponent implements OnInit {
  @Output() next: EventEmitter<SectionModel> = new EventEmitter<SectionModel>();
  selectedProject: ProjectType = PROJECTS[0];
  projects = PROJECTS;

  constructor() {
  }

  ngOnInit(): void {
  }

  scrollToNext(event: any): void {
    this.next.emit({
      icon: 'bar_chart',
      id: 'third',
      selected: false
    });
  }

  setSelectedPage(page: ProjectType): void {
    this.selectedProject = page;
  }

}
