import {Component, HostListener, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {SectionModel} from '../../models/section.model';

@Component({
  selector: 'app-scroll-navigation',
  templateUrl: './scroll-navigation.component.html',
  styleUrls: ['./scroll-navigation.component.scss']
})
export class ScrollNavigationComponent implements OnInit, OnChanges {
  sections: SectionModel[] = [
    {
      icon: 'insert_emoticon',
      id: 'first',
      selected: true
    },
    {
      icon: 'diamond',
      id: 'second',
      selected: false
    },
    {
      icon: 'bar_chart',
      id: 'third',
      selected: false
    },
    {
      icon: 'lightbulb',
      id: 'forth',
      selected: false
    },
    {
      icon: 'message',
      id: 'fifth',
      selected: false
    },
  ];
  @Input() next?: SectionModel;

  @HostListener('window:scroll', ['$event'])
  onWindowScroll(): void {
    const pos = (document.documentElement.scrollTop || document.body.scrollTop) + document.documentElement.offsetHeight;
    const max = document.documentElement.scrollHeight;
    if (pos > 976) {
      this.sections.map(s => s.selected = s.id === 'second');
    }
    if (pos > 1952) {
      this.sections.map(s => s.selected = s.id === 'third');
    }
    if (pos > 2980) {
      this.sections.map(s => s.selected = s.id === 'forth');
    }
    if (pos > 3920) {
      this.sections.map(s => s.selected = s.id === 'fifth');
    }
    if (pos < 976 || pos === 976) {
      this.sections.map(s => s.selected = s.id === 'first');
    }
  }

  constructor() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes && changes.next) {
      if (this.next) {
        this.scrollToSelectedSection(this.next);
      }
    }
  }

  ngOnInit(): void {
    this.scrollToSelectedSection({
      icon: 'insert_emoticon',
      id: 'first',
      selected: true
    });
  }

  scrollToSelectedSection(section: SectionModel): void {
    this.sections.map(s => s.selected = s.id === section.id);
    const itemToScroll = document.getElementById(section.id);
    if (itemToScroll) {
      itemToScroll.scrollIntoView({block: 'center', behavior: 'smooth'});
    }
  }


}
